<template>
  <main class='content container alert alert--info' v-if='productLoading'> ...</main>
  <main class='content container alert alert--error' v-else-if='!productData'>
     ...
  </main>
  <main class='content container' v-else>
    <div class='content__top'>
      <BaseBreadcrumbs :pages='pages'/>
    </div>

    <section class='item'>
      <div class='item__pics pics'>
        <div class='pics__wrapper'>
          <img width='570' height='570' :src='product.image' :alt='product.title'>
        </div>
      </div>

      <div class='item__info'>
        <span class='item__code'>: {{ product.id }}</span>
        <h2 class='item__title'>{{ product.title }}</h2>
        <div class='item__form'>
          <form class='form' action='#' method='POST' @submit.prevent='addToCart'>
            <b class='item__price'>{{ product.price | numberFormat }} </b>

            <fieldset class='form__block'>
              <legend class='form__legend'>:</legend>
              <BaseColorsToggler :colors='colors' :selectedColor.sync='selectedColor'/>
            </fieldset>

            <fieldset class='form__block'>
              <legend class='form__legend'>  :</legend>

              <ul class='sizes sizes--primery'>
                <li class='sizes__item'>
                  <label class='sizes__label'>
                    <input class='sizes__radio sr-only' type='radio' name='sizes-item' value='32'>
                    <span class='sizes__value'>32gb</span>
                  </label>
                </li>
                <li class='sizes__item'>
                  <label class='sizes__label'>
                    <input class='sizes__radio sr-only' type='radio' name='sizes-item' value='64'>
                    <span class='sizes__value'>64gb</span>
                  </label>
                </li>
                <li class='sizes__item'>
                  <label class='sizes__label'>
                    <input class='sizes__radio sr-only' type='radio' name='sizes-item' value='128'
                           checked=''>
                    <span class='sizes__value'>128gb</span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class='item__row'>
              <BaseCounter :min='1'
                           :max='100'
                           :count.sync='productAmount'
                           v-model.number='productAmount'
              />

              <button
                class='button button--primery'
                type='submit'
                :disabled='productAddSending'> 
              </button>
            </div>
            <div class='alert alert--success' v-show='productAdded'>   </div>
            <div class='alert alert--info' v-show='productAddSending'>
                 ...
            </div>
          </form>
        </div>
      </div>

      <div class='item__desc'>
        <ul class='tabs'>
          <li class='tabs__item'>
            <a class='tabs__link tabs__link--current'></a>
          </li>
          <li class='tabs__item'>
            <a class='tabs__link' href='#'></a>
          </li>
          <li class='tabs__item'>
            <a class='tabs__link' href='#'></a>
          </li>
          <li class='tabs__item'>
            <a class='tabs__link' href='#'>  </a>
          </li>
        </ul>

        <div class='item__content'>
          <p>
             GPS, , BEIDOU Galileo  QZSS<br>
              <br>
              Bluetooth Smart, ANT+  Wi-Fi<br>
              <br>
          </p>

          <a href='#'> </a>

          <h3> ?</h3>

          <p>
            Wahoo ELEMNT BOLT GPS   ,    
            ,    . Wahoo ELEMNT BOLT GPS
                ANT+,     . 
              ,     .    
                 ,    
             .    ,  
               .      
                .
          </p>

          <h3></h3>

          <p>
             Wahoo ELEMNT BOLT  .    
            74,6 x 47,3 x 22,1 .     .    
             .         56
            .      ,      
              : ,   , 
             .. (     ).    
               IPX7.  ,     ,   
             ( 30 )        1 .
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<!-- style -->
<style lang='scss'>
@import '../../public/scss/index';

.item__row {
  margin-bottom: 20px;
}
</style>

<!-- script -->
<script>
import { mapActions } from 'vuex';
import BaseBreadcrumbs from '@/components/Base/BaseBreadcrumbs.vue';
import BaseCounter from '@/components/Base/BaseCounter.vue';
import BaseColorsToggler from '@/components/Base/BaseColorsToggler.vue';
import numberFormat from '@/helpers/numberFormat';

export default {
  name: 'ProductPage',
  components: {
    BaseBreadcrumbs,
    BaseCounter,
    BaseColorsToggler,
  },
  filters: {
    numberFormat,
  },
  watch: {
    selectedColor(value) {
      this.selectedColor = value;
    },
    productAmount(value) {
      this.productAmount = value;
    },
    productData(value) {
      this.selectedColor = value.colors[0].id;
    },
    '$route.params.id': {
      immediate: true,
      handler(productId) {
        this.productLoading = true;
        this.loadProduct({ productId })
          .then((res) => {
            if (res.status !== 200) throw new Error('Not found');
            this.productData = res.data;
          })
          .catch((err) => {
            this.$router.push('/404');
            console.error(err);
          })
          .then(() => {
            this.productLoading = false;
          });
      },
    },
  },
  data() {
    return {
      selectedColor: 0,
      productAmount: 1,
      productData: null,
      productLoading: false,
      productAdded: false,
      productAddSending: false,
    };
  },
  methods: {
    ...mapActions([
      'addProductToCart',
      'loadProduct',
    ]),
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;

      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
        // colorId: this.selectedColor,
      }).then(() => {
        this.productAdded = true;
        this.productAddSending = false;
      });
    },
  },
  computed: {
    product() {
      return { ...this.productData, image: this.productData.image.file.url };
    },
    category() {
      return this.productData.category;
    },
    colors() {
      return this.productData.colors;
    },
    pages() {
      return [
        {
          name: 'main',
          title: this.category.title,
        },
        {
          name: 'product',
          title: this.product.title,
          params: {
            id: this.product.id,
          },
        },
      ];
    },
  },
};
</script>
